





















import { Component, Vue } from 'vue-property-decorator';

interface Option {
    title?: String,
    value?: String,
    type?: String,
    cancelText?: String,
    confirmText?: String
}

@Component({name:'Modal'})
export default class Modal extends Vue {
    private title: String = '';
    private value: String = '';
    private inputValue: String|Number = '';
    private cancelText: String = '';
    private confirmText: String = '';
    private showFlag: Boolean = false;
    private type: String = 'message';
    private placeholder: String = '';
    private resolve: Function;
    private reject: Function;

    private cancelHandler():void {
        this.showFlag = false;
        this.inputValue = '';
        this.reject&&this.reject();
    }

    private confirmHandler():void {
        let data;
        if (this.type=='input'){
            data = {
                action: 'confirm',
                value: this.inputValue
            }
        }else{
            data = {
                action: 'confirm'
            }
        }
        this.showFlag = false ;
        this.resolve&&this.resolve(data);
        this.inputValue = '';
    }

    /**
     * message
     */
    public message(value?: string, title: string = ''):Promise<any>{
        this.value = value;
        this.title = title;
        this.type = 'message';
        this.showFlag = true;
        return new Promise((resolve,reject)=>{
            this.resolve = resolve;
            this.reject = reject;
        })
    }

    public input(placeholder?: string, title: string = ''):Promise<any>{
        this.placeholder = placeholder;
        this.title = title;
        this.type = 'input';
        this.showFlag = true;
        return new Promise((resolve,reject)=>{
            this.resolve = resolve;
            this.reject = reject;
        })
    }

    public dispose(option):Promise<any>{
        this.title = option.title || this.title;
        this.value = option.value || this.value;
        this.type = option.type || this.type;
        this.cancelText = option.cancelText || this.cancelText;
        this.confirmText = option.confirmText || this.confirmText;
        this.showFlag = true;
        return new Promise((resolve,reject)=>{
            this.resolve = resolve;
            this.reject = reject;
        })
    }

}
