













import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Toast extends Vue {
    private type: String = ''; // success, error, info, text
    private title: String = '';
    private showFlag: Boolean = false ;

    public success(title?: string, time: number = 2000): Promise<any>{
        this.title = title;
        this.type = 'success';
        this.showFlag = true;
        return new Promise((resolve)=>{
            setTimeout(()=>{
                this.showFlag = false ;
                resolve();
            },time)
        })
    }

    public error(title?: string, time: number = 2000): Promise<any>{
        this.title = title;
        this.type = 'error';
        this.showFlag = true;
        return new Promise((resolve)=>{
            setTimeout(()=>{
                this.showFlag = false ;
                resolve();
            },time)
        })
    }

    public info(title?: string, time: number = 2000): Promise<any>{
        this.title = title;
        this.type = 'info';
        this.showFlag = true;
        return new Promise((resolve)=>{
            setTimeout(()=>{
                this.showFlag = false ;
                resolve();
            },time)
        })
    }

    public text(title?: string, time: number = 2000): Promise<any>{
        this.title = title;
        this.type = '';
        this.showFlag = true;
        return new Promise((resolve)=>{
            setTimeout(()=>{
                this.showFlag = false ;
                resolve();
            },time)
        })
    }
}
