
import Vue from 'vue';
import loading from './loading.vue';

let newInstance;
const LoadingInstance = Vue.extend(loading); // 

// 
const initInstance = ()=>{
    newInstance = new LoadingInstance(); // 
    // $elDombody
    document.body.appendChild(newInstance.$mount().$el);
};

if (!newInstance){
    initInstance();
}

export default newInstance;