


















































import { Component, Vue,Prop } from 'vue-property-decorator';
import selectPicker from './selectorPicker.vue';
import multiPicker from './multiPicker.vue';
import regionPicker from './regionPicker.vue';
import datePicker from './datePicker.vue';

@Component({
    components:{
        selectPicker,
        multiPicker,
        regionPicker,
        datePicker,
    }
})
export default class Picker extends Vue {

    @Prop({type:Array,default:()=>{return []}}) readonly data;
    @Prop(String) readonly rangeKey; // key
    @Prop(String) readonly mode;  // pickermultiSelector; region

    @Prop({type:[String,Number],default:1999}) readonly startYear; // 
    @Prop({type:[String,Number],default:2030}) readonly endYear; // 
    @Prop([String,Number,Object,Array]) readonly value; 

    // data
    private showMask: boolean = false;// 

    private successHandler(val){
        this.$emit('input',val);
    }

}
