






import { Component, Vue, Prop, Watch, Provide} from 'vue-property-decorator';

@Component({
    name: 'Form',
    created(){
        // form.fields
        this.$on('vueUI.form.addField',field=>{
            if(field){
                this.fields.push(field);
            }
        });

        // 
        this.$on('vueUI.form.removeField',field=>{
            if(field.prop){
                this.fields.splice(this.fields.indexOf(field.prop),1);
            }
        })
    }
})
export default class Form extends Vue {
    @Prop(Object) readonly model;
    @Prop(Object) readonly rules;

    @Provide() Form = this;

    private fields: Array<any> = []; // 

    @Watch('rules')
    rulesHandler(){
        this.fields.forEach(field =>{
            field.removeValidateEvents();
            field.addValidateEvents();
        })
    }

    /**
     * 
     * validate
     */
    public validate(): Promise<any> {
        return new Promise((resolve,reject)=>{
            let valid = true; // 
            let count = 0; // 
            // fieldscallback
            if (this.fields.length==0){
                resolve(true);
                return;
            }

            let invalidFields = {}; // 
            this.fields.forEach(field => {
                field.validate('').then((data)=>{
                    if (data.validateMessage){
                        valid = false; // 
                        resolve(data.validateMessage);
                    }
                    invalidFields = Object.assign({},invalidFields,field);
                    if (++count === this.fields.length){
                        resolve(true);
                    }
                })
            })
        })
    }

}
