













import { Component, Vue, Prop, Watch } from 'vue-property-decorator';

@Component({
    name:'Search'
})
export default class Search extends Vue {
    @Prop(String) readonly value;
    @Prop({type:String,default:''}) readonly placeholder;
    @Prop(String) readonly type; // round()

    private searchKey :string = '';

    @Watch('value')
    valueHandler(){
        this.searchKey = this.value;
    }

    private changeHandler():void {
        this.$emit('input',this.searchKey);
    }

    private searchHandler():void{
        if(!this.searchKey){
            (this as any).$toast.error('');
            return;
        }
        this.$emit('search',this.searchKey);
    }

    private cancelHandler():void{
        this.searchKey = '';
        this.changeHandler();
        this.$emit('clear');
    }
}
