import Vue from 'vue';
import preview from './preview.vue';

let newInstance;
const PreviewInstance = Vue.extend(preview); // 

// 
const initInstance = ()=>{
    newInstance = new PreviewInstance(); // 
    // $elDombody
    document.body.appendChild(newInstance.$mount().$el);
};

if (!newInstance){
    initInstance();
}

// 
/**
 * v-preview: 
 */
const preViewHandler = ()=>{
    const defaultImg = '';
    Vue.directive('preview',{
        bind: bindHandler
    })
}

const bindHandler=(el, binding, vnode)=>{
    let error;
    if(!binding.value){
        // 
        error = true;
        el.classList.add('vueUI-not-src');
        el.src = Vue.prototype.vueUIConfig.errorImg;
        return;
    }

    el.src = binding.value;
    el.onerror = ()=>{
        el.src = Vue.prototype.vueUIConfig.errorImg;
        el.classList.add('vueUI-img-error');
        error = true;
    }

    el.addEventListener('click',()=>{
        if(error) return;
        Vue.prototype.$preview.show(binding.value);
    })
}

export default {
    instance: newInstance,
    preview: preViewHandler
};